.sp-arrow {
  position: absolute;
  display: block;
  width: 20px;
  height: 30px;
  cursor: pointer; }

.sp-vertical .sp-arrow {
  transform: rotate(90deg); }

.sp-horizontal .sp-previous-arrow {
  left: 20px;
  right: auto; }

.sp-horizontal.sp-rtl .sp-previous-arrow {
  right: 20px;
  left: auto; }

.sp-horizontal .sp-next-arrow {
